.Navigation {
    text-align: center;
    width: 100%;
}

.Navigation ul {
    list-style: none;
    line-height: 60px;
    font-size: 20px;
    font-family: 'Merriweather', serif;
    padding-right: 30px;
    text-align: right;
}
.Navigation li {
    cursor: pointer;
    margin-right: 5px;
    height: 100%;
}

.Navigation li:hover {
    font-size: 22px;
    background-color: black;
    cursor: pointer;
}

.Navigation a {
    display: inline-block;
    color: white;
    text-decoration: none;
    width: 100%;
}

.Navigation a:active{
    font-size: 26px;
    background-color: black
}

@media(max-width:500px) {
    .Navigation {
        margin-left: 0px;
        text-align: center;
        width: 100%;
    }
    .Navigation ul {
        display: flex;
        list-style: none;
        line-height: 10px;
        font-size: 16px;
        justify-content: space-evenly;
        text-align: left;
    }
    .Navigation li {
        cursor: pointer;
        margin-right: 5px;
        height: 100%;
    }
    .Navigation a {
        color: white;
        text-decoration: none;
        width: auto;
    }
    .Navigation a:active, .Navigation a:hover {
        font-size: 20px;
    }
}