.Header{
    display: flex;
    flex-direction: column;
    align-items:center;
}

.Image{
    margin:20px 10px;
    height: 150px;
    width:150px;
    border-radius: 50%;
}

.Name{
    font-family: 'Raleway', sans-serif;
    margin: 0px;
    font-size: 1.7em;
    text-align: center;
}

.Info{
    font-family: 'Raleway', sans-serif;
    text-align: center;
    margin: 0;
    line-height: 15px;
}

@media (max-width:500px){
    .Header{
        display: inline;
        margin: 10px;
    }
    
    .Image{
        margin:3px 3px;
        height: 50px;
        width:50px;
        position: absolute;
        border-radius: 50%;
    }
    hr{
        display: none;
    }
    .Header p{
        text-align: left;
        font-size: 14px;
        margin-top: 10px;
        margin-left: 70px;
        line-height: 5px;
    }
    .Degree{
        display: none;
    }
}