.RightComponent{
    display: flex;
    flex-flow: column;
    padding:0px 20px;
}

h3{
    text-align: center;
    font-family: 'Raleway', sans-serif;
    font-size: 30px;
    line-height: 0;
}
@keyframes fadein {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.Details{
    padding-left:20px;
}

@media(max-width:500px){
    .RightComponent{
        display: flex;
        flex-flow: column;
        padding:0px 0px;
    }
    
    h3{
        text-align: left;
        font-family: 'Raleway', sans-serif;
        font-size: 20px;
        line-height: 0;
    }
    @keyframes fadein {
        from{
            opacity: 0;
        }
        to{
            opacity: 1;
        }
    }
    
    .Details{
        padding-left:10px;
    } 
}