.App {
  display: flex;
  height:700px;
  width:90%;
  margin:auto;
  margin-top:10px;
  box-shadow: 1px 5px 20px 10px #888888;
}

.Left{
  width:30%;
}

.Right{
  width:100%;
  overflow: auto;
  background-color: rgb(247,242,242)
}

@media only screen and (max-width:500px){
  .App {
    display: flex;
    flex-flow: column;
    height:700px;
    width:90%;
    font-size: 14px;
    box-shadow: 0px;
  }
  
  .Left{
    width:100%;
    height:100px;
    position:sticky;
  }
  
  .Right{
    min-height: 600px;
    background-color: rgb(247,242,242);
  }
}