.Contact {
    flex-wrap: wrap;
    font-family: 'Raleway', sans-serif;
    height: 500px;
    background-repeat: no-repeat;
    background-image: url("../../../assets/australia.png");
    background-size: 250px;
    background-position: left bottom;
}


.Copied {
    animation: cssAnimation  2s;
    visibility: hidden;
    opacity: 0;
}

@keyframes cssAnimation {
    from {
        visibility: visible;
        opacity: 1;
    }
    to {
        visibility: visible;
        opacity: 0;
    }
}