.LeftComponent{
    display: flex;
    flex-direction: column;
    height:100%;
    background-color: rgba(0,0,0,0.898);
    color: white;
}

footer{
    width:80px;
    float: right;
    align-self: flex-end;
}

@media(max-width:500px){
    .LeftComponent{
        display: flex;
        /* flex-direction: column; */
        height:100%;
        background-color: rgba(0,0,0,0.898);
        color: white;
        position: relative;
    }
    
    footer{
        width:30px;
        float: right;
        position: absolute;
        top:0;
    }
}