.Project {
    width: 200px;
    height: 200px;
    position: relative;
    background-color: white;
    color: white;
    background-size: 100%;
    box-shadow: 5px 10px 10px gainsboro;
}

.Desc {
    display: flex;
    visibility: hidden;
    width: 100%;
    height: 100%;
    background-color: black;
    display: flex;
    flex-flow: column wrap;
    align-content: center;
}

@keyframes fadeout {
    from {
        opacity: 0;
    }
    to {
        opacity: 0.5;
    }
}

@keyframes slideup {
    from {
        opacity: 0;
        transform: translateY(5%);
    }
    to {
        opacity: 0.8;
        transform: translateY(0%);
    }
}

.Project a {
    text-decoration: none;
    color: black;
    width: 100px;
    height: 20px;
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    vertical-align: "bottom";
    background-color: lightgray;
    border-radius: 10px;
    padding: 2px;
    font-size: 12px;
    text-align: center;
}

@keyframes slideColor {
    from {
        background-color: lightgray;
    }
    to {
        background-color: darkgray;
    }
}

.Project a:hover {
    animation: slideColor 0.3s;
    font-weight: bold;
    background-color: darkgray
}

.Project:hover>.Desc {
    visibility: visible;
    animation: slideup 0.3s ease-in;
    opacity: 0.8;
}

.Project img {
    display: inline-block;
    position: absolute;
    width: 100%;
    height:100%;
}

@media(max-width:500px){
    .Project {
        margin:10px;
        width: 150px;
        height: 150px;
        position: relative;
        background-color: white;
        color: white;
        background-size: 100%;
        box-shadow: 5px 10px 10px gainsboro;
    }
}