.Dropdown {
    margin: 20px;
    width: 80%;
    height: auto;
    transition: all 5s ease;
    position: relative;
}



/* onClick use  this to transform */
@keyframes show {
    0% {
        opacity: 0;
        transform: translateY(-100vh)
    }
    50% {
        /* transform: translateY(-2%); */
        opacity: 0.3;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
} 

.drpButton {
    width: 100%;
    margin: auto;
    height: 40px;
    line-height: 40px;
    background-color: rgba(0, 0, 0, 0.850);
    color: white;
    font-size: 16px;
    text-align: center;
    position: relative;
    z-index: 1;
}

.drpButton:hover {
    background-color: black;
    font-size: 17px;
    cursor: pointer;
}

.drpButton:hover + ul{
    display: inline-block;
}

.Dropdown ul {
    box-sizing: border-box;
    list-style-type: none;
    padding: 12px;
    margin: auto;
    width: 100%;
    display: none;
    overflow: hidden;
    background-color: white;
    color: black;
    left:0;
    position: absolute;
    transition: all 5s ease-out;
    animation: show 0.5s;
    z-index: 10;
}